<template>
  <swiper
      :slidesPerView="1"
      :spaceBetween="16"
      :loop="false"
      :breakpoints="{
        '450': {
          slidesPerView: 2
        },
        '576': {
          slidesPerView: 3
        },
        '992': {
          slidesPerView: 4
        },
        '1200': {
          slidesPerView: 5
        },
        '1400': {
          slidesPerView: 6
        },
        '1500': {
          slidesPerView: 7
        },
        '1600': {
          slidesPerView: 8
        },
      }"
      :navigation="true"
      :modules="modules"
      class="home-slider slider-top"
    >
    <div class="home-slider-top">
      <div class="home-slider-title">Top games</div>
      <a @click="seeMore" class="see-more-btn link-btn link-btn-secondary">See all</a>
    </div>
    <swiper-slide v-for="(item, key) in filterGames" :key="key">
      <div class="game-slider-item">
        <div class="game">
          <div class="game-top">
            <div class="game-block-img">
              <img :src="item.imgUrl" :alt="item.name" class="game-img">
            </div>
            <div class="game-action">
              <div class="game-btns">
                <div class="game-play">
                  <button class="btn game-btn-play">Play</button>
                </div>
              </div>
            </div>
          </div>
          <div class="game-bottom">
            <div class="game-title">
              <a href="#" class="link-no-style" disabled>{{ item.name }}</a>
            </div>
          </div>
        </div>
      </div>
    </swiper-slide>
  </swiper>
</template>

<style>
.home-slider {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: flex-end;
  position: relative;
  margin-bottom: 50px;
  min-height: 180px;
}
.home-slider-top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  padding-right: 96px;
  width: 100%
}
.home-slider-title {
  font-size: 14px;
  font-weight: 800;
  line-height: 1;
  margin: 0;
  text-align: center;
  display: inline-flex;
  align-items: center;
  color: #ffffff;
}
.see-more-btn {
  min-height: 40px;
  min-width: 112px;
}
.swiper-button-next.swiper-button-disabled, .swiper-button-prev.swiper-button-disabled {
  opacity: 1;
}
.slider-top .swiper-button-next,
.slider-top .swiper-button-prev,
.slider-new .swiper-button-next,
.slider-new .swiper-button-prev,
.slider-live .swiper-button-next,
.slider-live .swiper-button-prev,
.slider-providers .swiper-button-next,
.slider-providers .swiper-button-prev  {
  background-color: #00224b;
  display: inline-flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  height: 40px;
  width: 40px;
  margin: 0;
  padding: 0;
  outline: none;
  top: 0px;
  transition: background-color .3s ease, color .3s ease;
  opacity: 1;
}
.slider-top .swiper-button-next:hover,
.slider-top .swiper-button-prev:hover,
.slider-new .swiper-button-next:hover,
.slider-new .swiper-button-prev:hover,
.slider-live .swiper-button-next:hover,
.slider-live .swiper-button-prev:hover,
.slider-providers .swiper-button-next:hover,
.slider-providers .swiper-button-prev:hover {
  background-color: #ffb81e;
  color: #000c1e;
}
.slider-top .swiper-button-next:after,
.slider-top .swiper-button-prev:after,
.slider-new .swiper-button-next:after,
.slider-new .swiper-button-prev:after,
.slider-live .swiper-button-next:after,
.slider-live .swiper-button-prev:after,
.slider-providers .swiper-button-next:after,
.slider-providers .swiper-button-prev:after {
  font-size: 14px;
  font-weight: 900;
}
.slider-top .swiper-button-prev.swiper-button-disabled:after,
.slider-top .swiper-button-next.swiper-button-disabled:after,
.slider-new .swiper-button-next.swiper-button-disabled:after,
.slider-new .swiper-button-prev.swiper-button-disabled:after,
.slider-live .swiper-button-next.swiper-button-disabled:after,
.slider-live .swiper-button-prev.swiper-button-disabled:after,
.slider-providers .swiper-button-next.swiper-button-disabled:after,
.slider-providers .swiper-button-prev.swiper-button-disabled:after {
  opacity: .5;
}
.slider-top .swiper-button-prev,
.slider-new .swiper-button-prev,
.slider-live .swiper-button-prev,
.slider-providers .swiper-button-prev {
  border-radius: 20px 0 0 20px;
  right: 40px;
  left: auto;
}
.slider-top .swiper-button-next,
.slider-new .swiper-button-next,
.slider-live .swiper-button-next,
.slider-providers .swiper-button-next {
  border-radius: 0 20px 20px 0;
  right: 0px;
}
/* game provider slide*/
.games-page-providers-slider-item {
  border-radius: 8px;
  overflow: hidden;
}
.games-page-providers-slider-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  height: 250px;
  position: relative;
}
.games-page-providers-slider-bg {
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  position: absolute;
  width: 100%;
}
.games-page-providers-slider-gradient {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: block;
}
.games-page-providers-slider-gradient.first {
  background: linear-gradient(180deg, rgba(232, 15, 28, 0) 42%, rgba(232, 15, 28, .8) 70.92%, #e80f1c);
}
.games-page-providers-slider-gradient.second {
  background: linear-gradient(180deg,rgba(208, 147, 58, 0) 42%,rgba(208, 147, 58, .8) 70.92%,#d0933a);
}
.games-page-providers-slider-gradient.third {
  background: linear-gradient(180deg,rgba(247, 115, 131, 0) 42%,rgba(247, 115, 131, .8) 70.92%,#f77383);
}
.games-page-providers-slider-gradient.fouth {
  background: linear-gradient(180deg,rgba(109, 128, 2, 0) 42%,rgba(109, 128, 2, .8) 70.92%,#6d8002);
}
.games-page-providers-slider-logo {
  display: flex;
  height: 46px;
  max-width: 200px;
  margin-bottom: 20px;
  -o-object-fit: contain;
  object-fit: contain;
  position: relative;
}
</style>

<script>
import { Swiper, SwiperSlide } from 'swiper/vue'
import 'swiper/css'
import 'swiper/css/navigation'
import { Navigation } from 'swiper'
import { ref, inject } from 'vue'
import { useRouter } from 'vue-router'
export default {
  components: {
    Swiper,
    SwiperSlide
  },
  setup () {
    const games = inject('tempGames')
    const filterGames = ref([])
    const router = useRouter()
    const filter = () => {
      games.forEach((item) => {
        if (item.isTop) {
          filterGames.value.push(item)
        }
      })
    }
    const seeMore = () => {
      router.push({
        name: 'top'
      })
    }
    filter()
    return {
      modules: [Navigation],
      games,
      filterGames,
      filter,
      seeMore
    }
  }
}
</script>
