<template>
  <swiper
      :slidesPerView="1"
      :spaceBetween="16"
      :loop="false"
      :breakpoints="{
        '450': {
          slidesPerView: 2
        },
        '576': {
          slidesPerView: 3
        },
        '992': {
          slidesPerView: 4
        },
        '1200': {
          slidesPerView: 5
        },
        '1400': {
          slidesPerView: 6
        },
        '1500': {
          slidesPerView: 7
        },
        '1600': {
          slidesPerView: 8
        },
      }"
      :navigation="true"
      :modules="modules"
      class="home-slider slider-new"
    >
    <div class="home-slider-top">
      <div class="home-slider-title">New games</div>
      <a @click.prevent="seeMore" class="see-more-btn link-btn link-btn-secondary">See all</a>
    </div>
    <swiper-slide v-for="(item, key) in filterGames" :key="key">
      <div class="game-slider-item">
        <div class="game">
          <div class="game-top">
            <div class="game-block-img">
              <img :src="item.imgUrl" :alt="item.name" class="game-img">
            </div>
            <div class="game-action">
              <div class="game-btns">
                <div class="game-play">
                  <button class="btn game-btn-play">Play</button>
                </div>
              </div>
            </div>
          </div>
          <div class="game-bottom">
            <div class="game-title">
              <a href="#" class="link-no-style" disabled>{{ item.name }}</a>
            </div>
          </div>
        </div>
      </div>
    </swiper-slide>
  </swiper>
</template>

<script>
import { Swiper, SwiperSlide } from 'swiper/vue'
import 'swiper/css'
import 'swiper/css/navigation'
import { Navigation } from 'swiper'
import { ref, inject } from 'vue'
import { useRouter } from 'vue-router'
export default {
  components: {
    Swiper,
    SwiperSlide
  },
  setup () {
    const games = inject('tempGames')
    const filterGames = ref([])
    const router = useRouter()
    const filter = () => {
      games.forEach((item) => {
        if (item.isNew) {
          filterGames.value.push(item)
        }
      })
    }
    const seeMore = () => {
      router.push({
        name: 'new'
      })
    }
    filter()
    return {
      modules: [Navigation],
      games,
      filterGames,
      filter,
      seeMore
    }
  }
}
</script>
