    sendData (item) {
      if (item.name === 'All providers') {
        this.emitter.emit('sendProvider', '')
        this.providers.shift()
        this.provider = ''
        this.$refs.menuProvidersContent.classList.toggle('show')
      } else {
        if (this.providers[0].name === 'All providers') {
          this.provider = item.name
          this.emitter.emit('sendProvider', item.name)
          this.$refs.menuProvidersContent.classList.toggle('show')
        } else {
          this.provider = item.name
          this.emitter.emit('sendProvider', item.name)
          this.$refs.menuProvidersContent.classList.toggle('show')
          this.providers.unshift({ name: 'All providers' })
        }
      }
    },
